import React from 'react'
import { graphql, Link, useStaticQuery } from 'gatsby'
// import BackgroundImage from 'gatsby-background-image'
import { ReadMore } from './readmore'
import Img from "gatsby-image"

const PageOneSecondary = () => {
	const data = useStaticQuery(
		graphql`
          query {
            allMarkdownRemark(
                sort: { fields: [frontmatter___date], order: DESC }
            ) {
                totalCount
                edges {
                  node {
                    frontmatter {
                      date
                      title
                      summary
					  type
					  key
                      featured
                      image {
                        id
                        childImageSharp {
                            fluid(quality: 90, maxWidth: 1920) {
                              ...GatsbyImageSharpFluid_withWebp
                            }
                          }
                      }
                    }
                    fields {
                      slug
                    }
                  }
                }
              }
          }
        `
	)

	let blurbs = [];
	for (let i = 0; i < data.allMarkdownRemark.edges.length; i++) {
		if (data.allMarkdownRemark.edges[i].node.frontmatter.type === 'blurb') {
			blurbs.push(data.allMarkdownRemark.edges[i]);
		}
	}

	return (
		<Link to={blurbs[0].node.fields.slug} style={{ textDecoration: 'none' }}>
			<Img fluid={blurbs[0].node.frontmatter.image.childImageSharp.fluid} />
			<div style={{marginTop: 15, paddingLeft: 15, paddingRight: 15}}>
				<p className="blurbKey">#{blurbs[0].node.frontmatter.key}</p>
				<h3 className="titleText primary">{blurbs[0].node.frontmatter.title}</h3>
				<p className="summaryText">{blurbs[0].node.frontmatter.summary}</p>
				<span style={{fontSize: '9pt', color: 'grey'}}>{blurbs[0].node.frontmatter.date}</span>
			</div>
			{/*<BackgroundImage
            Tag="section"
            fluid={blurbs[0].node.frontmatter.image.childImageSharp.fluid}
            backgroundColor={`#040e18`}
            style={{
                width: '100%',
                backgroundPosition: 'bottom center',
                backgroundRepeat: 'no-repeat',
                backgroundSize: 'cover',
                minHeight: 400,
                position: 'relative',
                cursor: 'pointer'
            }}
          >
			<div style={{
				padding: 15,
				backgroundColor: 'rgba(0,0,0,0.75)',
				color: 'white',
				position: 'absolute',
				bottom: 0,
				margin: 15,
				maxWidth: 450
			}}>
				
			</div>
            {/*</BackgroundImage>*/}
        </Link >
      )
      
}

export default PageOneSecondary;
