import React from "react"
import { Link } from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { Grid } from "@material-ui/core"
import ProjectBox from "../components/projectbox"
import PageOnePrimary from "../components/pageoneprimary"
import { graphql } from 'gatsby'
import 'fontsource-roboto';
import { Build, Sms } from "@material-ui/icons"
import PageOneSecondary from "../components/pageonesecondary"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub, faInstagram, faLinkedin } from '@fortawesome/free-brands-svg-icons';
import { faIdCard } from "@fortawesome/free-solid-svg-icons"

const IndexPage = ({ data }) => (
	<Layout>
		<SEO title="Home" />
		<div style={{marginBottom: 30}}>
			<Grid container spacing={2}>
				<Grid item md={6} xs={12}>
					<PageOnePrimary />
				</Grid>
				<Grid item md={6} xs={12}>
					<Grid container spacing={2}>
						<PageOneSecondary />
					</Grid>
					<div style={{marginTop: 15}}>
						<Link to="/blurbs/" style={{textDecoration: 'none'}}>
							<div className="hoverBackground">
							<Sms style={{color: 'green', marginTop: 'auto', marginBottom: 'auto',}} />
								<span style={{ marginLeft: 5, color: 'black' }}>
								Read More Blurbs
								</span>
							</div>
						</Link>
					</div>
				</Grid>
			</Grid>
		</div>
		<div style={{marginBottom: 40}}>
			<div style={{marginBottom: 10, fontSize: '16pt', fontWeight: '525', borderTop: '2px solid black', paddingTop: 10}}>Featured Projects</div>
			<Grid container spacing={2}>
				{
					data.allMarkdownRemark.edges.map(({ node }) => {
						if(node.frontmatter.type === "project" && node.frontmatter.featured) {
							const title = node.frontmatter.title || node.fields.slug
							return(
								<ProjectBox 
									key={node.fields.slug}
									title={title}
									summary={node.frontmatter.summary}
									path={node.fields.slug}
									image={node.frontmatter.image.childImageSharp.fluid}
							
								/>
							)
						} else {
							return null;
						}
					})
				}
			</Grid>
			<div style={{margin: 10}}>
				<Link ></Link>
				<Link to="/projects" style={{textDecoration: 'none'}}>
					<div className="hoverBackground">
						<Build style={{color: 'orange', marginTop: 'auto', marginBottom: 'auto',}} />
						<span style={{ marginLeft: 5, color: 'black' }}>See More Projects</span>
					</div>
				</Link>
			</div>
		</div>
		<div style={{marginBottom: 40}}>
			<div style={{marginBottom: 10, fontSize: '16pt', fontWeight: '525', borderTop: '2px solid black', paddingTop: 10}}>Links</div>
			<Grid container spacing={2}>
				<Grid item md={3} sm={4} xs={12}>
					<Link to="/resume" style={{textDecoration: 'none'}}>
						<div className="hoverBackground" target="_blank" style={{textAlign: 'center', display: 'block'}}>
							<FontAwesomeIcon icon={faIdCard} size="4x" color="grey" />
							<div>Resume</div>
						</div>
					</Link>
				</Grid>
				<Grid item md={3} sm={4} xs={12}>
					<Link to="https://github.com/Mistweaver" target="_blank" style={{textDecoration: 'none'}}>
						<div className="hoverBackground" style={{textAlign: 'center', display: 'block'}}>
							<FontAwesomeIcon icon={faGithub} size="4x" color="green" />
							<div>Github</div>
						</div>
					</Link>
				</Grid>
				<Grid item md={3} sm={4} xs={12}>
					<Link to="https://www.linkedin.com/in/bryce-egley-77828411b/" target="_blank" style={{textDecoration: 'none'}}>
						<div className="hoverBackground" style={{textAlign: 'center', display: 'block'}}>
							<FontAwesomeIcon icon={faLinkedin} size="4x" color="blue" />
							<div>LinkedIn</div>
						</div>
					</Link>
				</Grid>
				<Grid item md={3} sm={4} xs={12}>
					<Link to="https://www.instagram.com/bryceegley/" target="_blank" style={{textDecoration: 'none'}}>

						<div className="hoverBackground" style={{textAlign: 'center', display: 'block'}}>
							<FontAwesomeIcon icon={faInstagram} size="4x" color="purple" />
							<div>Instagram</div>
						</div>
					</Link>
				</Grid>
			</Grid>
			{/*<Link to="/my-files/">Look at my files</Link> <br />
			<Link to="/using-typescript/">Go to "Using TypeScript"</Link>*/}
		</div>
		
	</Layout>
)

export default IndexPage


export const query = graphql`
  query {
    allMarkdownRemark(
		sort: { fields: [frontmatter___date], order: DESC }
	) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
			date(formatString: "DD MMMM, YYYY")
			type
			featured
			image {
				id
				childImageSharp {
					fluid(quality: 90, maxWidth: 1920) {
					  ...GatsbyImageSharpFluid_withWebp
					}
				  }
			  
			}
			summary
		  }
		  fields {
            slug
          }
        }
      }
    }
  }
`

