import React from 'react'
import { graphql, Link, useStaticQuery } from 'gatsby'
import { Grid } from '@material-ui/core'
import Img from "gatsby-image"

const PageOneSecondary = () => {
	const data = useStaticQuery(
		graphql`
          query {
            allMarkdownRemark(
                sort: { fields: [frontmatter___date], order: DESC }
            ) {
                totalCount
                edges {
                  node {
                    frontmatter {
                      date
                      title
                      summary
                      type
                      featured
					  key
                      image {
                        id
                        childImageSharp {
                            fluid(quality: 90, maxWidth: 1920) {
                              ...GatsbyImageSharpFluid_withWebp
                            }
                          }
                      }
                    }
                    fields {
                      slug
                    }
                  }
                }
              }
          }
        `
	)

	let blurbs = [];
	for (let i = 0; i < data.allMarkdownRemark.edges.length; i++) {
		if (data.allMarkdownRemark.edges[i].node.frontmatter.type === 'blurb') {
			blurbs.push(data.allMarkdownRemark.edges[i]);
		}
	}

	return (
		<>
			{
				blurbs.length > 1 &&
				<Grid item xs={12}>
					<Link to={blurbs[1].node.fields.slug} style={{ textDecoration: 'none' }}>
						<Grid container spacing={1}>
							<Grid item sm={6} xs={12}>
								<Img fluid={blurbs[1].node.frontmatter.image.childImageSharp.fluid} style={{margin: 0}} />
							</Grid>
							<Grid item md={5} sm={6} xs={12}>
								<div style={{padding: 10}}>
									<p className="blurbKey">#{blurbs[1].node.frontmatter.key}</p>
									<h3 className="titleText secondary">{blurbs[1].node.frontmatter.title}</h3>
									<p className="summaryText">{blurbs[1].node.frontmatter.summary}</p>
									<span style={{fontSize: '9pt', color: 'grey'}}>{blurbs[1].node.frontmatter.date}</span>
								</div>
							</Grid>
						</Grid>
					</Link>
				</Grid>
			}
			{
				blurbs.length > 2 &&
				<Grid item xs={12}>
					<Link to={blurbs[2].node.fields.slug} style={{ textDecoration: 'none' }}>
						<Grid container spacing={1}>
							<Grid item sm={6} xs={12}>
								<Img fluid={blurbs[2].node.frontmatter.image.childImageSharp.fluid} style={{margin: 0}} />
							</Grid>
							<Grid item md={5} sm={6} xs={12}>
								<div style={{padding: 10}}>
									<p className="blurbKey">#{blurbs[2].node.frontmatter.key}</p>
									<h3 className="titleText secondary">{blurbs[2].node.frontmatter.title}</h3>
									<p className="summaryText">{blurbs[2].node.frontmatter.summary}</p>
									<span style={{fontSize: '9pt', color: 'grey'}}>{blurbs[2].node.frontmatter.date}</span>
								</div>
							</Grid>
						</Grid>
					</Link>
				</Grid>
			}


		</>
	)

}

export default PageOneSecondary;
